





















 import util from '../../common/util'
import navigate from '../../components/navigator'
export default {
    data() {
        return {
            isShowBack:true,
            movieId:'',
            cityCode:'',
            titleText:'',
            showStar:true,
            showShare:true,
            movieDetail:'',
            releaseDate:{},
            showAllStory:'story-txt',
            arrowDirection:'',
            buyBtnText:'',
            comment:{
                mini:{
                    list:[]
                },
                plus:{
                    total:0
                }
            },
            pageIndex:1,
            videoList:[]
        }
    },
    onLoad(option) {
        let self = this;
        self.titleText = option.movieName;
        self.movieId = option.movieId;
    },
    onReady(){
        let self = this;
        util.request({
            url:'https://api-m.mtime.cn/Movie/Video.api?pageIndex='+self.pageIndex+'&movieId='+self.movieId
        }).then(res=>{
            self.videoList = res.videoList;
        })
    },
    components:{
        navigate
    },
    methods:{
        videoEnd(){
            let self = this;

        },
        videoPlay(){
            let self = this;

        }
    }
}
